import { IBaseMsg } from "../IMsg";

export type suggestionRequestMap = Record<number,
    {
        requestid: number,
        suggestionCount: number,
        validSuggestionCount: number,
        suggestions: Record<number, { suggestionid: number, valid: boolean, appliedByUser?: boolean, undoByUser?: boolean }>
    }>;

export interface IResponsiveSuggestionsQuickEditor extends IBaseMsg {
    type: "quickEditor.info.responsiveSuggestions",
    requestCount: number,
    succesfulRequestCount?: number,
    layoutSuggestionRequestMap?: suggestionRequestMap,
    totalSuggestionCount?: number,
    validSuggestionCount?: number,
    appliedSuggestionCount?: number,
    undoedSuggestionCount?: number,
}

export interface ICodeAssistantQuickEditor extends IBaseMsg {
    type: "quickEditor.info.codeAssistant",
    refactorRequestMap?: Record<number, { refactorid: number, valid: boolean, appliedByUser?: boolean, dismissedByUser?: boolean }>,
    totalRefactorCount?: number,
    validRefactorCount?: number,
    appliedRefactorCount?: number,
    dismissedRefactorCount?: number,
}
